.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    padding-top: 16px;
}

.heading {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.img { 
    border: 1px solid;
    border-radius: 4px;
}

.login-form {
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: 288px;
    padding: 8px;
    border: 1px solid;
    border-radius: 4px;
    background-color: #fff;
}

input {
    height: 24px;
    width: 270px;
}

.buttons-bar {
    display: flex;
    justify-content: space-around;
}

button.disabled {
    pointer-events: none;
}

@media screen  and (max-width: 399px) {
    .login-form {
     width: 272px
    } 
 
    input {
     width: 256px
    }
 }